import { PagosConstants } from "../constants/pagos.constants"

const initialState = {
    list_pagos: [],
}

export const pagosReducer = (state = initialState, action) => {
    switch (action.type) {
        case PagosConstants.Initial:
            return {
                ...state,
                list_pagos:[]
            }
        case PagosConstants.getAll:
            return {
                ...state,
                list_pagos: action.payload
            }    
        default: return { ...state }
    }
}